import React from "react";
import "../style/style.scss";
import Contacto from "../img/contacto.png";
class Formulario extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      nombre: "",
    };
  }
  render() {
    return (
      <section class="l-section third-color s-py-4 fondo">
        <div class="row-gap s-gap-2 m-gap-4">
          <div class="s-column second-color s-main-center lg-main-start lg-cross-start s-center  ed-grid lg-grid-2">
            <div>
                <img src="https://cdn.pixabay.com/photo/2016/01/10/22/52/letters-1132703__340.png" alt="mensaje"/>
              <h2 className="text">{`Hola ¿Como estas?  ${this.state.nombre} espero que este bien. Gracias
               por visitarnos, le contactaremos muy pronto.
              Multisite
              `}</h2>
            </div>

            <div className="ed-item ed-containe">
              <form>
                <h1 className="text">Escribenos</h1>
                <div clasName="form-item ed-item">
                  <input
                    autocomplete
                    type="text"
                    required
                    placeholder="Ingresa tu nombre o nombre de la empresa"
                    onChange={(e) =>
                      this.setState({
                        nombre: e.target.value,
                      })
                    }
                  />
                </div>
                <br></br>
                <div clasName="form-item ed-item">
                  <input
                    autoComplete
                    clasName="form-item"
                    type="email"
                    required
                    placeholder="Ingresa tu correo electronico"
                  />
                </div>
                <br></br>
                <div clasName="form-item ed-item">
                  <input
                    autocomplete
                    clasName="form-item"
                    type="tel"
                    placeholder="Ingresa tu telefono"
                  />
                </div>
                <br></br>
                <div clasName="form-item ed-item">
                  <label autocomplete clasName="form-item ed-item"></label>
                  <select>
                    <option>¿Motivo por el cual esta aqui?</option>
                    <option>Para crear mi web</option>
                    <option>Hablar directamente con la empresa</option>
                    <option>¿Quiero Saber mas de que se trata?</option>
                    <option>Todas la anteriores</option>
                  </select>
                </div>
                <br></br>
                <div clasName="form-item">
                  <input
                    autocomplete
                    class="button second-color"
                    type="submit"
                    value="Enviar"
                  />
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    );
  }
}
export default Formulario;
